*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  // tap highlights removing
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}
.background {
  background-size: 100%;
  height: 100%;
  width: 100%;
  opacity: 0.2;
  background-position: center center;
  background: cover;
  background: no-repeat;
  z-index: -100;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}
.title {
  font-size: 50px;
  color: rgb(0, 0, 0);
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
  margin-top: 30px;
}
.footer {
  margin-bottom: 16px;
}
.msg {
  text-align: center;
  font-weight: 600;
}

@media (max-width: 1024px) {
  .title {
    font-size: 24px;
  }
  .background {
    background-position: center center;
  }
}
@media (min-width: 1900px) {
  .title {
    font-size: 70px;
  }
  .background {
    background: repeat;
  }
}

///spinner
.loader,
.loader:before,
.loader:after {
  background: #000000;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loader {
  color: #000000;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: "";
}
.loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
